.header__logo {
    margin-top: 5px;
    width: 50px;
    height: 50px;
    cursor: pointer;
    transition: opacity .3s linear;
}

.header__logo:hover {
    opacity: .7;
}

@media screen and (max-width: 768px) {
    .header__logo {
        margin-top: 3px;
        width: 32px;
        height: 32px;
    }
}